<template>
    <div id="second">
        <input type="file" multiple @change="inFile">
        <button @click="save">데이터 저장하기</button>
    </div>
</template>

<script>
export default {
  data () {
    return {
      file: null
    }
  },
  methods: {
    inFile (event) {
      console.log(event)
      this.file = event.target.files[0]
    },
    save () {
      let formData = new FormData()
      if (this.file != null && this.file[0] != null) {
        formData.append('files', this.file)
      }
    }
  }
}
</script>



